<template>
  <div class="pie_chart">
    <div class="title">案件阶段</div>
    <div ref="pieEcharts" style="height:300px"></div>

    <div class="pie_footer">
      <ul>
        <li style="color:#6CA5FF">1599/件</li>
        <li style="color:#6CA5FF">已组庭 40%</li>
      </ul>
      <ul>
        <li style="color:#175BE7">999/件</li>
        <li style="color:#175BE7">已立案 30%</li>
      </ul>
      <ul>
        <li style="color:#D2E4FF">599/件</li>
        <li style="color:#D2E4FF">已开庭 20%</li>
      </ul>
      <ul>
        <li style="color:#0080FF">99/件</li>
        <li style="color:#0080FF">已结庭 10%</li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      pieOption: {
        tooltip: {
          trigger: "item",
          formatter: "{b} : {c} ({d}%)"
        },
        series: [
          {
            type: "pie",
            radius: "65%",
            center: ["50%", "50%"],
            selectedMode: "single",
            // 立案#185AE7  结 #037FFF 组庭 #6DA4FF 开庭 #D4E3FF
            color: ["#6DA4FF", "#185AE7", "#D4E3FF", "#037FFF"],
            data: [
              {
                value: 1599,
                name: "已组庭",
                label: {
                  color: "#E4CA93",
                  formatter: "{c}件案件占比({d}%)"
                }
              },
              {
                value: 999,
                name: "已立案",
                label: {
                  color: "#E4CA93",
                  formatter: "{c}件案件占比({d}%)"
                }
              },
              {
                value: 599,
                name: "已开庭",
                label: {
                  color: "#E4CA93",
                  formatter: "{c}件案件占比({d}%)"
                }
              },
              {
                value: 99,
                name: "已结案",
                label: {
                  color: "#E4CA93",
                  formatter: "{c}件案件占比({d}%)"
                }
              }
            ],

            label: {
              normal: {
                textStyle: {
                  color: "#E4CA93",
                  fontSize: 12
                }
              }
            },
            labelLine: {
              normal: {
                length: 30,
                lineStyle: {
                  color: "#E4CA93"
                }
              }
            },
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      }
    };
  },
  mounted() {
    // 初始化饼图
    this.initPie();
  },
  methods: {
    initPie() {
      let chart = this.$refs["pieEcharts"];
      if (chart) {
        const myChart = this.$echarts.init(chart);
        myChart.setOption(this.pieOption);
        window.addEventListener("resize", function() {
          myChart.resize();
        });
      }
    }
  }
};
</script>
<style lang="less" scoped>
.pie_chart {
  height: 438px;
  background-color: #fff;
  padding-top: 10px;
  margin-bottom: 10px;
  border-radius: 10px;
  .title {
        font-size: 18px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: rgba(250, 201, 66, 1);
        padding-left: 14px;
      }
  .pie_footer {
    display: flex;
    justify-content: space-between;
    padding: 0 144px;
    font-size: 18px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    ul {
      border-right: 1px solid #f1f1f1;
      padding-right: 61px;
      li:first-child {
        font-size: 18px;
      }
      & li:last-child {
        font-size: 12px;
      }
    }
    ul:last-child {
      border: none;
      padding-right: 0;
    }
  }
}
</style>