            <li class="agency_item" v-for="(item,index) in agency" :key="index">
              <img :width="item.width" :height="item.height" :src="item.img" alt />
              <span class="text">{{item.name}}</span>
              <div class="number">{{item.number}}</div>
              <!-- active -->
            </li>
          </ul>
        </div>

        <!-- 饼图 -->
        <pie-chart />
        
        <!-- 预警提醒 -->
