        </div>
      </div>

      <!-- 日历 -->
      <div class="home_right">
        <div class="picker_box">
          <div class="picker">
            <DatePicker
              :transfer="true"
              size="large"
              type="date"
              :open="true"
              placeholder="Select date"
            ></DatePicker>
          </div>
          <!-- 日程表 -->
          <div class="schedule">
            <div class="date">日程</div>
            <Icon type="ios-arrow-down" />
          </div>
          <ul class="schedule_list">
            <li v-for="(item,index) in things" :key="index">
              <Icon type="ios-checkmark-circle-outline" />
              <span class="time">{{item.time}}</span>
              <span class="things">{{item.name}}</span>
            </li>
          </ul>
        </div>
        <div class="add_things">
          <Icon type="ios-add" />
        </div>
      </div>
    </div>
    <!-- echarts 柱状图 -->
    <category-chart />

  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import categoryChart from './categoryChart'
import pieChart from './pieChart'
export default {
  name: "home",
  components:{
    categoryChart,
    pieChart
  },
  data() {
    return {
      agency: [
        {
          name: "预受理案件",
          number: 45,
          img: require("../../assets/images/agency4.png")
        },
        {
          name: "缴费待审核",
          number: 45,
          img: require("../../assets/images/agency5.png")
        },
        {
          name: "待立案案件",
          number: 45,
          img: require("../../assets/images/agency2.png")
        },
        {
          name: "立案审核案件",
          number: 45,
          img: require("../../assets/images/agency6.png")
        },
        {
          name:"案件分配",
          number:0,
          img:require('../../assets/images/agency7.png')
        },
        {
          name:"企业审核",
          number:0,
          img:require('../../assets/images/agency19.png')
        },
        {
          name:"个人审核",
          number:0,
          img:require('../../assets/images/agency8.png')
        },
        {
          name:"管辖权异议",
          number:0,
          img:require('../../assets/images/agency9.png')
        },
        {
          name:"组庭呈报",
          number:0,
          img:require('../../assets/images/agency10.png')
        },
        {
          name:"选择开庭方式",
          number:0,
          img:require('../../assets/images/agency11.png')
        },
        {
          name:"文书处理",
          number:0,
          img:require('../../assets/images/agency12.png')
        },
        {
          name:"申请补正",
          number:0,
          img:require('../../assets/images/agency13.png')
        },
        {
          name:"仲裁员回避",
          number:0,
          img:require('../../assets/images/agency14.png')
        },
        {
          name:"撤案待处理",
          number:0,
          img:require('../../assets/images/agency15.png')
        },
        {
          name:"组庭案件",
          number:0,
          img:require('../../assets/images/agency16.png')
        },
        {
          name:"组庭审核",
          number:0,
          img:require('../../assets/images/agency17.png')
        },
        {
          name:"缴费待通知",
          number:0,
          img:require('../../assets/images/agency18.png')
        },
        
      ],
      things: [
        { time: "09:00AM", name: "日程名称、备注、预约会议室" },
        { time: "09:00AM", name: "日程名称、备注、预约会议室" },
        { time: "09:00AM", name: "日程名称、备注、预约会议室" },
        { time: "09:00AM", name: "日程名称、备注、预约会议室" },
        { time: "09:00AM", name: "日程名称、备注、预约会议室" }
      ]
    };
  },
  computed: {},
  mounted() {
    this.setWidthImg(this.agency);
  },
  methods: {
    async setWidthImg(arr) {
      let newarr = [];
      for (var i = 0; i < arr.length; i++) {
        const result = await this.getImgInfo(arr[i].img);
        if(arr[i].name=='企业审核'){
          // 企业审核的图标不是二倍图 所有*2
          result.width=result.width*2;
          result.height=result.height*2;
        }
        newarr.push(Object.assign(arr[i], result));
      }
      // console.log(newarr)
      this.agency = newarr;
    },
    getImgInfo(url) {
      return new Promise((resolve, reject) => {
        let img = new Image();
        img.src = url;
        img.onload = function() {
          resolve({
            width: img.width / 2,
            height: img.height / 2
          });
        };
        img.onerror = function() {
          reject(new Error("图片加载错误！"));
        };
      });
    }
  }
};
</script>
